html, body {
  user-select: none;
  background-color: #FFF7EF;
  font-family: 'Roboto', sans-serif, 'Google Noto Sans TC';
  color: #444444;
}

input[type="checkbox"]:checked {
  accent-color: #ed1b23;
}

::-webkit-scrollbar {
  width: 4px; /* 垂直卷軸 */
  height: 4px; /* 水平卷軸 */
}

::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background-color: #777;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.flex-row-center {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.h100 {
  height: 100%;
  max-height: 100%;
}

.w100 {
  width: 100%;
  max-width: 100%;
}

.red-font {
  color: #ff0000;
}

.header {
  @extend .flex-row;
  width: 100%;
  height: 60px;
  font-size: 32px;
  color: #ffffff;
  line-height: 60px;
  align-items: center;
  justify-content: center;
  background-color: #ed1b23;
}

.header-text {
  cursor: pointer;
  width: fit-content;
}

.app-container {
  @extend .flex-column;
  height: 100vh;
  width: 100vw;
}

.page-container {
  @extend .flex-row;
  height: 100%;
  overflow: hidden;
  justify-content: center;
}

.container {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  max-width: 1470px;
  padding: 20px 60px 0 60px;
}

.title-container {
  @extend .flex-row;
  height: 40px;
  align-items: center;
  justify-content: space-between;
}

.enlarge-button:hover {
  transform: scale(1.05); 
}

.icon-button {
  cursor: pointer;
  transition: transform 0.2s;
}

.icon-button:hover {
  color: #A51218;
  transform: scale(1.1); 
}

.icon-button-style {
  @extend .icon-button;
  @extend .flex-column-center;
  height: 80px;
  min-width: 180px;
  color: #A51218;
  padding: 0 40px;
  border-radius: 6px;
  background-color: #FFFCF9;
  box-shadow: 1px 2px 5px #826073;
}

.center-block {
  @extend .flex-row;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}


.project-wrapper {
  width: 100%;
  height: calc(100% - 42px);
  padding: 10px;
  margin-top: 10px;
  box-sizing: border-box;
  overflow-y: auto;
}

.project-container {
  display: inline-block;
  width: 250px;
  height: 340px;
  margin: 20px;
  padding: 10px 20px;
  border-radius: 4px;
  box-shadow: 1px 2px 5px #826073;
  background-color: #FFFCF9;
  cursor: pointer;
  transition: transform 0.2s;
}

.project-container:hover {
  transform: scale(1.04); 
}

.name-container {
  @extend .flex-row;
  justify-content: space-between;
  width: 100%;
  height: 30px;
  font-size: 22px;
  line-height: 30px;
  color: #444;
  font-weight: 500;
  font-family: 'Roboto' sans-serif;
}

.icon-button-container {
  @extend .flex-row-center;
  gap: 4px;
}

.project-img {
  height: 120px;
  margin: 10px 0;
}

.project-img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.note-container {
  height: 80px;
  overflow: auto;
  padding: 6px 10px;
  margin: 6px 0 10px 0;
  box-shadow: inset 0px 0px 4px 1px #aaaaaa;
  background-color: #ffffff;
}

.note-container pre {
  margin: 0;
  font-family: inherit;
}

.button-container {
  @extend .flex-row;
  gap: 10px;
}

.lower-right-button-container {
  @extend .flex-row-center;
  gap: 10px;
  width: auto;
  justify-content: end;
}

.title-style {
  @extend .flex-row;
  align-items: end;
  color: #000;
  font-size: 26px;
}

.title-name {
  max-width: 400px;
  font-weight: 600;
  display: inline-block;
}

.title-count {
  font-size: 16px;
  margin-left: 16px;
}

.first-title-container {
  margin: 0 20px;
}

.second-title-container {
  @extend .flex-row;
  height: 24px;
  align-items: center;
}

.second-title-style {
  color: #000;
  font-size: 20px;
  margin-right: 10px;
}

.dataSource-wrapper {
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  flex-direction: column;
  padding: 0 10px;
}

.dataSource-content {
  @extend .flex-column;
  height: calc(100% - 70px);
  margin: 10px 0;
  gap: 20px;
  /* border: 1px red solid; */
}

.dataSource-layer-container {
  @extend .flex-column;
  height: 100%;
  min-height: 150px;
  flex: 3;
  position: relative;
  padding: 10px 20px;
  border-radius: 4px;
  background-color: #FFFCF9;
  box-shadow: 1px 2px 5px #826073;
}

.dataSource-layer-wrapper {
  @extend .flex-row;
  height: calc(100% - 40px);
}

.dataSource-layer {
  @extend .flex-column;
  flex: 4;
  margin: 6px;
  height: 100%;
  overflow-y: auto;
  position: relative;
  background-color: #ffffff;
  box-shadow: inset 0px 0px 4px 1px #aaaaaa;
}

.dataSource-overlay {
  @extend .flex-row;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background-color: #00000005;
}

.dataSource-layer-wide {
  @extend .dataSource-layer;
  flex: 5;
}

.dataSource-item {
  @extend .flex-row;
  height: 43px;
  min-height: 43px;
  font-size: 16px;
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  line-height: 43px;
  cursor: pointer;
  padding: 0 10px;
  border-bottom: 1px solid #CCCCCC;
}

.dataSource-item:hover {
  background-color: #00000009
}

.dataSource-item-selected {
  background-color: #00000009;
}

.dataSource-item-selected:hover {
  background-color: #00000014;
}

.dataSource-item input {
  cursor: pointer;
}

.dataSource-item label {
  @extend .flex-row;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.dataSource-text {
  flex: 1;
  width: calc(100% - 45px);
}

.dataSource-cart {
  padding: 12px 20px;
  height: 100%;
  min-height: 100px;
  flex: 2;
  width: auto;
  border-radius: 4px;
  box-shadow: 1px 2px 5px #826073;
  background-color: #FFFCF9;
}

.selected-path-container {
  height: calc(100% - 44px);
  overflow-y: auto;
  margin: 10px 4px;
}

.selected-path {
  @extend .flex-row;
  margin-bottom: 4px;
}

.delete-button {
  margin-right: 6px;
}

.view-button {
  margin-left: 4px;
}

.select-all-button-container {
  @extend .flex-row;
  bottom: 0;
  width: 100%;
  height: 30px;
  position: absolute;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  cursor: pointer;
}

.select-all-button {
  width: 100%;
  height: 100%;
}

.select-all-button:hover {
  background-color: #A51218;
}

.dataSource-path {
  height: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.loading-icon {
  border-radius: 50%;
  width: 16px;
  height: 16px;
  display: inline-block;
  border: 3px solid #c0c0c0;
  border-top-color: #555;
  animation: spin 1s infinite linear;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes pulse {
  50% {
    background: rgba(255, 255, 255, 0.4);
  }
}

.loading-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
}

.loading-container {
  @extend .flex-column;
  top: 50%;
  left: 50%;
  width: 100%;
  position: absolute;
  align-items: center;
  transform: translate(-50%, -110%);
}

.linear-wrapper {
  height: 10px;
  width: 80%;
}

.dialog-container {
  @extend .flex-column;
  height: 100%;
  padding: 30px 40px;
}

.dialog-container form {
  @extend .flex-column;
  height: 100%;
}

.dialog-content {
  @extend .flex-column;
  flex: 1;
  padding: 10px 0;
}

.dialog-text {
  padding-top: 4px;
  font-size: 18px;
  flex: 1;
}

.dialog-content .input-name {
  min-width: 50px;
  padding-bottom: 4px;
  text-align: left;
}

.dialog-content input {
  width: 100%;
  height: 32px;
  padding: 0 6px;
  margin-right: 6px;
  color: #444444;
  font-size: 16px;
  box-sizing: border-box;
}

.dialog-content textarea {
  flex: 1;
  width: 100%;
  resize: none;
  color: #444444;
  font-size: 16px;
  padding: 4px 6px;
  margin-bottom: 16px;
  font-family: inherit;
  box-sizing: border-box;
}

.dialog-content input:focus {
  border: 2px solid #444444;
  border-radius: 4px;
  outline: none;
}

.dialog-content textarea:focus {
  border: 2px solid #444444;
  border-radius: 4px;
  outline: none;
}

.distribution-input {
  @extend .flex-row-center;
  flex: 1;
}

.border-div-container {
  @extend .flex-row;
  text-align: center;
  gap: 6px;
  height: calc(100% - 10px);
  background-color: #FFFFFF;
}

.border-div {
  @extend .flex-column;
  flex: 1;
  height: 100%;
  padding: 6px 12px 30px 12px;
  box-sizing: border-box;
  // border: 1px solid #aaaaaa;
  box-shadow: inset 0px 0px 2px 1px #aaaaaa;
  border-radius: 4px;
}

.view-XML-dialog {
  flex: 1;
  height: 100%;
  padding: 20px;
  display:flex;
  flex-direction: column;
  box-sizing: border-box;
}

.XML-img-container {
  @extend .flex-row;
  height: calc(100% - 54px);
  flex: 1;
  gap: 6px;
}

.XML-img-wrapper {
  flex: 1;
  padding: 4px 6px;
  overflow-y: auto;
  margin-top: 6px;
  background-color: #FFFFFF;
  box-shadow: inset 0px 0px 2px 1px #aaaaaa;
}

.XML-img {
  height: 70px;
  width: 100%;
  margin: 6px 0;
  cursor: pointer;
}

.XML-img img {
  height: 100%;
  width: 100%;
  object-fit: contain;
  transition: transform 0.2s;
}

.XML-img img:hover {
  transform: scale(1.08);
}

.required::after {
  content: ' * ';
  color: #ff0000;
  }

.loading-text {
  @extend .flex-row;
  height: 50px;
  font-size: 20px;
  font-weight: 400;
  color: #444;
  align-items: center;
  justify-content: center;
}

.loading-first-dots {
  display: inline-block;
  opacity: 0;
  animation: first-dot 2s infinite;
}

.loading-second-dots {
  display: inline-block;
  opacity: 0;
  animation: second-dot 2s infinite;
}

.loading-third-dots {
  display: inline-block;
  opacity: 0;
  animation: third-dot 2s infinite;
}
  
  @keyframes first-dot {
    0% {
      opacity: 0;
    }
    20% {
      opacity: 0;
    }
    40% {
      opacity: 1;
    }
    60% {
      opacity: 1;
    }
    80% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes second-dot {
    0% {
      opacity: 0;
    }
    20% {
      opacity: 0;
    }
    40% {
      opacity: 0;
    }
    60% {
      opacity: 1;
    }
    80% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes third-dot {
    0% {
      opacity: 0;
    }
    20% {
      opacity: 0;
    }
    40% {
      opacity: 0;
    }
    60% {
      opacity: 0;
    }
    80% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.attribute-page-container {
  @extend .flex-column;
  width: 100%;
  height: auto;
  margin: 30px 40px;
  box-sizing: border-box;
}

.attribute-page-content {
  @extend .flex-row;
  gap: 8px;
  width: 100%;
  height: calc(100% - 40px);
  margin-top: 10px;
}

.component-container {
  @extend .flex-column;
  overflow-y: auto;
  flex: 1;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  position: relative;
  text-align: center;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: #FFFCF9;
  box-shadow: 1px 2px 5px #826073;
}

.component-title {
  height: 36px;
  font-size: 18px;
  font-weight: 500;
  line-height: 36px;
  border-bottom: 1px solid #CCCCCC;
}

.component-content {
  flex: 1;
  overflow: auto;
  box-sizing: border-box;
  /* box-shadow: inset 0px 0px 4px 1px #aaaaaa; */
}

.component-item {
  @extend .flex-row;
  align-items: center;
  justify-content: space-between;
  padding: 0 6px;
  height: 36px;
  font-size: 14px;
  line-height: 36px;
  cursor: pointer;
  box-sizing: border-box;
  border-bottom: 1px solid #CCCCCC;
}

.component-item:hover {
  background-color: #00000009;
}

.component-item-selected {
  background-color: #00000009;
  box-shadow: inset 0px 0px 2px 1px #aaaaaa;
}

.component-item-selected:hover {
  background-color: #00000014;
}

.component-text {
  height: 100%;
  width: calc(100% - 18px);
  text-align: left;
  padding: 0 6px;
  box-sizing: border-box;
}

.resize-handle {
  position: absolute;
  top: 0;
  right: 0;
  width: 10px; /* 调整手柄的宽度 */
  height: 100%;
  background-color: #ff0000;
  cursor: col-resize; /* 显示调整大小的鼠标指针 */
  z-index: 1;
}

.attribute-container {
  @extend .flex-column;
  flex: 7;
  height: 100%;
  max-height: 100%;
  border-radius: 4px;
  background-color: #FFFCF9;
  box-shadow: 1px 2px 5px #826073;
}

.attribute-title {
  @extend .flex-row;
  @extend .component-title;
  padding: 0 20px;
  align-items: center;
  justify-content: space-between;
}

.attribute-content {
  @extend .flex-row;
  height: calc(100% - 44px);
  padding: 0 6px 6px 6px;
  gap: 6px;
}

.train-val-container {
  flex: 5;
  height: 100%;
  max-height: 100%;
}

.img-container {
  height: 100%;
  padding: 4px;
  overflow-y: auto;
  box-sizing: border-box;
}

.img-container-shadow {
  @extend .img-container;
  background-color: #ffffff;
  box-shadow: inset 0px 0px 4px 1px #aaaaaa;
}

.img-container-shadow-warn {
  @extend .img-container-shadow;
  box-shadow: inset 0px 0px 5px 1px #ffa500;
}

.golden-delete-container {
  @extend .flex-column;
  flex: 1;
  height: 100%;
  max-height: 100%;
}

.train-val-wrapper {
  @extend .flex-column;
  height: 50%;
  max-height: 50%;
}

.train-val-title {
  height: 30px;
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  text-align: center;
}

.train-val-title span{
  color: #ffa500;
  font-weight: 500;
}

.pass-ng-container {
  @extend .flex-row;
  gap: 6px;
  height: calc(100% - 30px);
  box-sizing: border-box;
}

.pass-ng-wrapper {
  @extend .flex-column;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  background-color: #ffffff;
  box-shadow: inset 0px 0px 4px 1px #aaaaaa;
}

.pass-ng-wrapper-warn {
  @extend .pass-ng-wrapper;
  box-shadow: inset 0px 0px 5px 1px #ffa500;
}

.pass-ng-title {
  @extend .flex-row-center;
  height: 30px;
  font-size: 14px;
}

.golden-wrapper {
  @extend .flex-column;
  height: 25%;
  max-height: 25%;
}

.delete-wrapper {
  @extend .flex-column;
  height: 75%;
}

.drag-item {
  width: 60px;
  height: 60px;
  margin: 4px;
  display: inline-block;
  transition: transform 0.2s;
}

.drag-item-golden {
  @extend .drag-item;
  width: 80%;
  height: 80%;
}

.drag-item:hover {
  transform: scale(1.1)
}

.drag-item img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.more-button {
  @extend .flex-row;
  width: 36px;
  height: 36px;
  border-radius: 4px;
  align-items: center;
  justify-content: center;
  background-color: #eee7e0;
  box-shadow: 0px 2px 2px 1px #00000004;
}

.loading-overlay {
  @extend .flex-column-center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  font-size: 24px;
  color: #ed1b23;
  line-height: 40px;
  background-color: rgba(255, 255, 255, 0.7);
}

.loading-spinner {
  border: 5px solid rgba(0, 0, 0, 0.3);
  border-top: 5px solid #ed1b23;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

.search-bar {
  height: 26px;
  font-size: 16px;
  padding-left: 10px;
  border-radius: 2px;
  border: 1px solid #AAAAAA;
}

.search-bar:focus {
  outline: none;
  border: 1px solid #777777;
}